import { Button, Chip, Container, InputAdornment, MenuItem, Stack, TextField, Typography, Menu } from '@mui/material';
import { Helmet } from 'react-helmet-async';
import { useSettingsContext } from 'src/components/settings';
import { color, decryptToken, formatObj } from 'src/utils/common';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { useState, useEffect } from 'react';
import { constants } from 'src/utils/constant';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import SearchIcon from '@mui/icons-material/Search';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import dayjs from 'dayjs';
import { DatePicker } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';

// sections

// ----------------------------------------------------------------------

export default function DayBook() {
    const settings = useSettingsContext();
    const [isLoading, setIsLoading] = useState(false)
    const [list, setList] = useState([]);
    const [tempData, setTempData] = useState([]);
    const [searchQuery, setSearchQuery] = useState('');
    const [date, setDate] = useState(dayjs(new Date()))
    const { enqueueSnackbar } = useSnackbar();
    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);

    const fetchData = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.reports.dayBook, { date: dayjs(date).format('YYYY-MM-DD') }, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setList(response.data.data);
                setTempData(response.data.data);
            }
        } catch (err) {
            console.log(err)
            enqueueSnackbar(err.error || 'Something went wrong!.', { variant: 'error' });
            setIsLoading(false);
        }
    };

    useEffect(() => {
        setIsLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [date]);

    const columns = [
        {
            name: 'Item Name',
            selector: (row) => {
                if (row.partyType === 'CUSTOMER' || row.partyType === 'VENDOR') {
                    return row.userName || 'N/A';
                }
                return row.category || 'N/A';
            },
            sortable: true,
        },
        {
            name: 'Transaction Type',
            selector: row => row.partyType,
            sortable: 'true',
        },
        {
            name: 'Invoice Number',
            selector: row => row.invoiceNumber,
            sortable: 'true',
        },
        {
            name: 'Amount',
            selector: row => formatObj.format(row.amount),
            sortable: 'true',
        },
        {
            name: 'Time',
            selector: row => {
                const timeString = new Date(row.createdAt).toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit', hour12: true });
                return timeString || 'N/A';
            },
            sortable: 'true',
        },
    ];
    const filteredItems = list.filter((data) =>
        (data?.userName && data.userName.toLowerCase().includes(searchQuery?.toLowerCase())) ||
        (data?.category && data.category.toLowerCase().includes(searchQuery?.toLowerCase()))
    );

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Helmet>
                <title> Reports: Day Book</title>
            </Helmet>

            <Typography variant="h4"> Day Book </Typography>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between", marginTop: "20px" }}>
                <Stack direction="row" spacing={1}>
                    <Chip
                        label={`Sales : ${list.length > 0 ? formatObj.format(list.filter((data) => data.partyType === 'CUSTOMER').reduce((accumulator, currentValue) => accumulator + currentValue.amount, 0)) : formatObj.format(0)}`}
                        variant="outlined"
                    />
                    <Chip
                        label={`Purchase : ${list.length > 0 ? formatObj.format(list.filter((data) => data.partyType === 'VENDOR').reduce((accumulator, currentValue) => accumulator + currentValue.amount, 0)) : formatObj.format(0)}`}
                        variant="outlined"
                    />
                    <Chip
                        label={`Expense : ${list.length > 0 ? formatObj.format(list.filter((data) => data.partyType === 'EXPENSE').reduce((accumulator, currentValue) => accumulator + currentValue.amount, 0)) : formatObj.format(0)}`}
                        variant="outlined"
                    />
                    <Chip
                        label={`Income : ${list.length > 0 ? formatObj.format(list.filter((data) => data.partyType === 'INCOME').reduce((accumulator, currentValue) => accumulator + currentValue.amount, 0)) : formatObj.format(0)}`}
                        variant="outlined"
                    />
                </Stack>

                <Stack>
                    <Button style={{ color: color.actionButton }} endIcon={<ArrowDropDownIcon />} variant='outlined' onClick={(e) => setAnchorEl(e.currentTarget)}>
                        ACTIONS
                    </Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >

                        <MenuItem onClick={() => {
                        }}>Print</MenuItem>

                        <MenuItem onClick={() => {
                        }}>Download</MenuItem>

                    </Menu>
                </Stack>
            </div>

            <div style={{ display: 'flex', justifyContent: 'flex-start', gap: '10px', marginTop: '20px' }}>
                <TextField
                    placeholder='Search'
                    size='small'
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
                <LocalizationProvider dateAdapter={AdapterDayjs} >
                    <DatePicker
                        value={date}
                        onChange={(newValue) => {
                            setDate(newValue)
                        }}
                        format="DD/MM/YYYY"
                        size="small"
                        slotProps={{ textField: { size: 'small' } }}
                    />
                </LocalizationProvider>
            </div>

            <div style={{ marginTop: '20px' }}>
                <DataTable
                    columns={columns}
                    data={filteredItems}
                    progressPending={isLoading}
                    pagination
                />

            </div>
        </Container>
    );
}
